import React, { useRef, useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import Swal from 'sweetalert2';
import styles from './paginaWeb/css/editperfil.module.css';
import axiosInstance from './AxiosConf/axiosconf';
import { useNavigate } from 'react-router-dom';

// Componentes
import Menu from './components/menuComponent';
import { usePerfil } from './components/publicacionUtils';

function Editperfil() {
    const nav = useNavigate();
    const perfil = usePerfil();
    const [formData, setFormData] = useState({
        nickname: '',
        desc: '',
        nombre: '',
        contrasea: ''
    });
    const [archivo, setArchivo] = useState(null);
    const [errors, setErrors] = useState({});
    const fileInputRef = useRef(null);
    const imgElementRef = useRef(null);

    // Obtener datos del perfil al cargar el componente
    useEffect(() => {
        axiosInstance.get('/perfilMenu')
            .then(response => {
                const data = response.data;
                setFormData({
                    nickname: data.nickname || '',
                    desc: data.descripcion || '',
                    nombre: data.nombre || '',
                    contrasea: '' // No traemos la contrasea por seguridad
                });
            })
            .catch(error => {
                console.error('Error al obtener la informacin del perfil:', error);
            });
    }, []);

    // Manejadores de eventos
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    const validateForm = () => {
        const newErrors = {};
        Object.keys(formData).forEach((key) => {
            if (!formData[key]?.trim() && key !== 'contrasea') { // La contrasea puede estar vaca si no se va a cambiar
                newErrors[key] = `${key.charAt(0).toUpperCase() + key.slice(1)} es requerido`;
            }
        });
        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleFileInputChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            const imgUrl = URL.createObjectURL(file);
            imgElementRef.current.src = imgUrl;
            setArchivo(file);
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (validateForm()) {
            const formDataToSend = new FormData();
            formDataToSend.append('nickname', formData.nickname);
            formDataToSend.append('desc', formData.desc);
            formDataToSend.append('nombre', formData.nombre);
            if (formData.contrasea.trim()) {
                formDataToSend.append('contrasenia', formData.contrasea);
            }
            if (archivo) {
                formDataToSend.append('foto', archivo);
            }

            // Para verificar si la contrasea se ha agregado correctamente
            for (let pair of formDataToSend.entries()) {
                console.log(pair[0] + ': ' + pair[1]);
            }

            axiosInstance.put('http://localhost:3001/actualizarPerfil', formDataToSend, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
                .then(response => {
                    Swal.fire({
                        title: 'Tu perfil se modific con xito',
                        text: '<3',
                        icon: 'success',
                        confirmButtonText: 'Yeiiiiii :DD'
                    }).then((result) => {
                        if (result.isConfirmed) {
                            nav('/dashboard');
                        }
                    });
                })
                .catch(error => {
                    if (error.response && error.response.status === 400) {
                        setErrors({ nickname: error.response.data });
                    } else {
                        console.error('Error al actualizar el perfil:', error);
                    }
                });
        }
    };

    return (
        <>
            <Menu perfil={perfil} />
            <div className="container">
                <form onSubmit={handleSubmit}>
                    <div className={`${styles["row"]} row`}>
                        <div id={styles.pfinfo} className="col-md-12" data-id="IDdelacuentaUwU">
                            <div id={styles.pfp}>
                                <img id={styles.pfPhoto} ref={imgElementRef} src={perfil.foto || "../../resources/pfp/lovers.jpeg"} />
                                <input type="file" id="newPFP" accept="image/*" style={{ display: "none" }} onChange={handleFileInputChange} ref={fileInputRef} />
                                <button id={styles.subirImagenBtn} className="btn btn-primary" type="button" onClick={() => fileInputRef.current.click()}>Subir imagen</button>
                            </div>
                            <div id='inputsTexts'>
                                <div id={styles.pfnick}>
                                    <h3>Nickname</h3>
                                    <input type="text" id={styles.inputs} placeholder="Ingrese su nickname aqu" name="nickname" value={formData.nickname} onChange={handleChange} />
                                    {errors.nickname && <p className={styles.error}>{errors.nickname}</p>}

                                    <h3>Descripcin</h3>
                                    <input type="text" id={styles.inputs} placeholder="Ingrese su descripcin aqu" name="desc" value={formData.desc} onChange={handleChange} />
                                    {errors.desc && <p className={styles.error}>{errors.desc}</p>}

                                    <h3>Nombre</h3>
                                    <input type="text" id={styles.inputs} placeholder="Ingrese su nombre aqu" name="nombre" value={formData.nombre} onChange={handleChange} />
                                    {errors.nombre && <p className={styles.error}>{errors.nombre}</p>}

                                    <h3>Contrasea</h3>
                                    <input type="password" id={styles.inputs} placeholder="Ingrese su contrasea aqu" name="contrasea" value={formData.contrasea} onChange={handleChange} />
                                    {errors.contrasea && <p className={styles.error}>{errors.contrasea}</p>}
                                </div>
                            </div>
                            <button id={styles.guardaInfo} className="btn btn-primary" type="submit">Guardar Cambios</button>
                        </div>
                    </div>
                </form>
            </div>
        </>
    );
}

export default Editperfil;


